.App {
  text-align: center;
  width: 300px;
  border: 1px solid #ebe7e7;
  height: 430px;
  margin: auto;
  -webkit-box-shadow: 4px 4px 8px #e1dddd;
          box-shadow: 4px 4px 8px #e1dddd;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%) translatex(-50%);
      -ms-transform: translateY(-50%) translatex(-50%);
          transform: translateY(-50%) translatex(-50%);
  left: 50%;
}
.title{
  background: #494545;
  color: white;
  padding: 6px;
  text-transform: uppercase;
}
.display{
  height: 40px;
  margin: 10px;
  text-align: right;
  font-size: 20px;
  color: #5c5c5c;
  overflow-y: hidden;
  white-space: nowrap;
}
.inputDisplay{
  margin: 5px;
}
.inputDisplayTxt{
  width: 100%;
  border: none;
  border-bottom: 2px solid  #cac7c7;
  display: block;
  height: 40px;
  font-size: 35px;
  color: #5c5c5c;
  text-align:right;
}
.inputDisplayTxt:focus{
  outline: none;
  border-bottom: 2px solid  #00BCD4;
}
.numButton{
      background: white;
      border: none;
      padding: 10px;
      width: 50px;
      height: 50px;
      border-radius: 7px;
      background-color: #e1e8e4;
      -webkit-box-shadow: 0px 5px 0px 0px #acbab2;
              box-shadow: 0px 5px 0px 0px #acbab2;
      font-size: 20px;
      cursor: pointer;
      margin: 10px;
}

.numButton:active{
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  -ms-transform: translate(0px, 5px);
      transform: translate(0px, 5px);
  -webkit-transform: translate(0px, 5px);
}
.numButton:focus{
  outline: none;
}

.action{
  background-color: #2196F3;
  -webkit-box-shadow: 0px 5px 0px 0px #0f70bd;
          box-shadow: 0px 5px 0px 0px #0f70bd;
  color: #fff;
}
.result{
  height: 120px;
  float: right;
  margin: 0px;
  margin-right: 20px;
  margin-top: 10px;
  background-color: #4CAF50;
  -webkit-box-shadow: 0px 5px 0px 0px #38793b;
          box-shadow: 0px 5px 0px 0px #38793b;
  color: #fff;
}
.clear{
  background-color: #F44336;
  -webkit-box-shadow: 0px 5px 0px 0px #ad2a21;
          box-shadow: 0px 5px 0px 0px #ad2a21;
  color: #fff;
}


